___CSS_LOADER_EXPORT___.push([module.id, `* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  overflow-x: hidden;
  font-family: "Nunito Sans";
}

body.scroll {
  overflow: hidden;
}

#likeButton {
  display: block;
  margin: 20px auto;
  padding: 13px 20px;
  font-size: 16px;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  transition: background-color 0.3s;
}

#likeButton.add {
  background-color: green;
  color: white;
  font-size: 20px;
}

#likeButton.remove {
  background-color: red;
  color: white;
  font-size: 20px;
}
